<template>
  <div id="note">
      <el-input type="textarea" autosize placeholder="请输入标题" v-model="title"></el-input>
      <div style="margin: 20px 0;"></div>
      <vue-html5-editor :content="content" :height="400" @change="updateData"></vue-html5-editor>
      <div style="margin: 20px 0;"></div>
      <el-button v-on:click="save">保存</el-button>
  </div>
</template>

<<script>
export default {
  data () {
      return {
        title: '',
        text : '',
        content: '',
      }
  },
  methods: {
    save : function () {
       this.$http.post('http://localhost:8082/?r=note/add', {foo: 'bar'}).then(response => {

    // get status
    response.status;

    // get status text
    response.statusText;

    // get 'Expires' header
    response.headers.get('Expires');

    // get body data
    this.someData = response.body;
    console.log(this.someData)

  }, response => {
    // error callback
  });
      
    },
    updateData : function(data) {
      this.text = data
    }
  }
}
</script>

<style>

</style>



