import Vue from 'vue'
import Router from 'vue-router'
import Indexsite from '@/components/Index'
import Url from '@/components/Url'
import Filesite from '@/components/File'
import Note from '@/components/Note'
import Detail from '@/components/Detail'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Indexsite,
      meta: {
        breadcrumb: '笔记列表',
      }
    },
    {
      path: '/note',
      name: 'Note',
      component: Note,
      meta: {
        breadcrumb: '添加笔记',
      }
    },
    {
      path: '/url',
      name: 'Url',
      component: Url,
      meta: {
        breadcrumb: '网站收藏夹',
      },
    },
    {
      path: '/file',
      name: 'File',
      component: Filesite,
      meta: {
        breadcrumb: '文件中转站',
      },
    },
    {
      path:'/detail/:id',
      name:'Detail',
      component: Detail,
    }
  ]
})



