<template>
  <div id="detail">
    <h1 v-text="title"></h1>
    <p class="am-article-meta">2017-08-01/<a href="javascript:void(0);">编辑</a></p>
    <div v-text="text"></div>
    <div style="margin-top:80px;"></div>
    
  </div>
</template>

<<script>
export default {
  data () {
      return {
        text: '123',
        title: '12313',
        textarea2: '',
        textarea3: ''
      }
  },
  created () {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData()
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      this.error = this.post = null
      this.loading = true
      // replace getPost with your data fetching util / API wrapper
    //   getPost(this.$route.params.id, (err, post) => {
    //     this.loading = false
    //     if (err) {
    //       this.error = err.toString()
    //     } else {
    //       this.post = post
    //     }
    //   })
    this.$data.text = this.$route.params.id
    }
  }
}
</script>

<style>
.am-article-meta {
    font-size: 0.8rem;
    line-height: 1.5;
    color: #999;
}
a {
    color: #0e90d2;
    text-decoration: none;
    background-color: transparent;
}
</style>



