<template>
  <div id="app">
    <el-menu theme="dark" :default-active="$route.path" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="/"><i class="el-icon-edit"></i><strong>云笔记</strong></el-menu-item>
      <el-menu-item index="/note">添加笔记</el-menu-item>
      <el-menu-item index="/url">网站收藏夹</el-menu-item>
      <el-menu-item index="/file">文件</el-menu-item>
    </el-menu>
    <div style="max-width:1000px;" class="n-center">
      <div style="margin-right:5px;margin-left:5px;">
        <div style="margin-top:15px;"></div>
       <el-breadcrumb separator="/" style="margin-bottom:15px;">
        <el-breadcrumb-item :to="{ path: '/' }">首页列表</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/add-utl'}">添加网址</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/note'}">添加笔记</el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb> 
      <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        this.$router.push(keyPath[0]);
      }
    }
  }
</script>

<style>
body {
  margin: 0 0 0 0;
}
.el-menu {
  border-radius: 0px;
}
.n-center {
  position: absolute;
  margin: auto;
  left: 0; right: 0;
  overflow: auto;
} 
.container{text-align:center;} 
.center{display:inline-block;} 
</style>



